import React, { useState } from 'react';

import RoomIcon from '@material-ui/icons/Room';
import ReactMapGL, { Marker } from 'react-map-gl';

import useWindowSize from '../hooks/useWindowSize';
import Image from '../types/Image';

interface Props {
  image: Image,
  allImages: Image[],
  setImage: (image: Image) => void,
  shotting: boolean,
}

function Map(props: Props) {
  const { image, allImages, setImage, shotting } = props;
  const { latitude, longitude, key } = image;
  const windowSize = useWindowSize();
  const [viewport, setViewport] = useState({
    latitude,
    longitude,
    width: 400,
    height: 400,
    zoom: 7,
  });
  return (
    <ReactMapGL
      {...Object.assign(viewport, {
        width: windowSize ? windowSize.width : 0,
        height: windowSize ? (windowSize.height - windowSize.width + 50) : 0,
      })}
      onViewportChange={setViewport}
      style={{ display: shotting ? 'none' : 'inherit', position: 'absolute', bottom: 0, width: '100%' }}
      mapStyle='mapbox://styles/mapbox/streets-v11'
    >
      {
        allImages.map(i => (
          <Marker
            latitude={i.latitude}
            longitude={i.longitude}
            key={i.key}
            offsetLeft={-20}
            offsetTop={-10}
          >
            {i.key === key ? <RoomIcon color='secondary' /> : <RoomIcon color='primary' onClick={() => setImage(i)} />}
          </Marker>
        ))
      }
    </ReactMapGL>
  )
}

export default Map;