import React, { useState, useEffect } from 'react';

import Kaodashi from './components/Kaodashi';
import Control from './components/Control';
import Map from './components/Map';
import useWindowSize from './hooks/useWindowSize';
import Image from './types/Image';
import data from './data.json';

function pickRandomImage(data: Image[]) {
  return data[Math.floor(Math.random() * Math.floor(data.length))];
}

function App() {
  const [shotting, setShotting] = useState(false);
  const [imageLoaded, setImageLoaded] = useState(false);
  const [image, setImage] = useState<Image | null>(null);

  const windowSize = useWindowSize();

  useEffect(() => {
    const params = new URLSearchParams(document.location.search.substring(1));
    const key = params.get('key');
    let pickedImage;
    if (key) {
      for (const image of data) {
        if (image.key === key) {
          pickedImage = image;
          break;
        }
      }
    }
    if (!pickedImage) {
      pickedImage = pickRandomImage(data);
    }
    setImage(pickedImage);
  }, [])

  if (!image || !windowSize) {
    return <div>Loading</div>;
  }

  return (
    <div style={{ width: '100%' }}>
      <Map
        image={image}
        allImages={data}
        setImage={(image: Image) => { setImageLoaded(false); setImage(image) }}
        shotting={shotting}
      />
      <Kaodashi
        shotting={shotting}
        setShotting={setShotting}
        image={image}
        imageLoaded={imageLoaded}
        setImageLoaded={setImageLoaded}
        windowWidth={windowSize.width}
      />
      {!shotting && imageLoaded && (
        <Control
          windowWidth={windowSize.width}
          takeScreenShot={() => setShotting(true)}
        />
      )}
    </div>
  );
}

export default App;
