import React from 'react';

import IconButton from '@material-ui/core/IconButton';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import { styled } from '@material-ui/core/styles';
import Box, { BoxProps } from '@material-ui/core/Box';

interface StyledBoxProps {
  windowWidth: number
}

interface Props {
  windowWidth: number
  takeScreenShot: () => void
}

const StyledBox = styled(({ windowWidth, ...other }: StyledBoxProps & Omit<BoxProps, keyof StyledBoxProps>) => <Box {...other} />)({
  display: 'flex',
  flexFlow: 'column',
  position: 'absolute',
  right: 5,
  borderRadius: 25,
  backgroundColor: 'rgba(255, 255, 255, 0.6)',
  top: (props: StyledBoxProps) => props.windowWidth / 2 - 48,
});

function Control(props: Props) {
  const {
    windowWidth,
    takeScreenShot,
  } = props;
  return (
    <StyledBox windowWidth={windowWidth}>
      <IconButton color='primary' onClick={takeScreenShot}>
        <PhotoCamera />
      </IconButton>
    </StyledBox>
  )
}

export default Control;